import { FC, ReactNode, useRef } from 'react';
import { Tree, Spin, Table, message } from 'antd';
import {
  FindRoleListDocument,
  useFindRoleListQuery,
  useFindRouterAllQuery,
  useFindRouterRoleListQuery,
  useRouterRoleBulkMutation,
  useRouterRoleDestroyMutation,
  useUpdRouterParentIdMutation,
} from 'generator/financial-system.operation';
import listToTree from 'l2t';
import {
  Maybe,
  RoleFragment,
  RouterFragment,
  RouterRoleFragment,
} from 'generator/financial-system';
import { useImmer } from 'use-immer';
import { nothing } from 'immer';
import ProCard from '@ant-design/pro-card';
import { useTranslation } from 'react-i18next';
import ProTable, { ActionType, ProColumns } from '@ant-design/pro-table';
import {
  filtersToWhere,
  generatorColumns,
  gqlErrorMessage,
  proSortToOrder,
  WhereType,
} from 'utils/antd-helper';
import { UseFetchDataAction } from '@ant-design/pro-table/lib/typing';
import { Link } from 'react-router-dom';
import { differenceBy, get, keys, toInteger } from 'lodash';
import { Select, Typography, Button, Popconfirm } from 'antd';
import { APP_NAME } from '../../data/static';
import Bluebird from 'bluebird';
const { Option } = Select;
const { Title } = Typography;

type SetColumnType = { key: keyof RoleFragment; [p: string]: any };
type FilterType = {
  [p in keyof RoleFragment]: WhereType;
};
const whereModel: FilterType = {
  roleCode: '_like',
  roleName: '_like',
};

const Router: FC = () => {
  const { t } = useTranslation();

  const routerData = useFindRouterAllQuery({ skip: true });

  const [appNameItem, setAppNameItem] = useImmer<Maybe<string>>(null);
  const [routerItem, setRoutertem] = useImmer<Maybe<string>>(null);

  // #region select
  const handleSelectSelect = (value: any, option: any) => {
    routerData
      .refetch({
        param: {
          where: { appName: value },
        },
      })
      .then((result) => {
        setAppNameItem((draft) => value);
        listToTreeData(result.data.routerAll as any);
        refProTable.current?.reload();
      });
  };
  // #endregion

  // #region tree
  const [treeData, setTreeData] = useImmer<
    Maybe<
      Array<
        Maybe<{
          value: string;
          title: string;
          parentId?: string | undefined;
          key: string;
          children: any;
        }>
      >
    >
  >(null);

  const [updRouterParentId] = useUpdRouterParentIdMutation();

  const listToTreeData = (data: Maybe<Array<RouterFragment>>) => {
    setTreeData((draft) => {
      if (!data || data.length === 0) {
        return nothing as any;
      }
      return listToTree<
        RouterFragment,
        { id: string; parentId: string; children: [] }
      >(data, 'id', 'parentId', 'children', (item) => ({
        value: item?.id,
        title: item?.displayTxt,
        parentId: item?.parentId,
        key: item.id,
      }));
    });
  };

  /**
   * 拖拽事件
   */
  const handleDrop = async (info: any) => {
    // 拖拽落下的位置 最外层到最里层
    const dragKey = info.dragNode.key;

    if (info.dropToGap) {
      // 顶级
      await updRouterParentId({ variables: { id: dragKey, parentId: null } });
    } else {
      await updRouterParentId({
        variables: { id: dragKey, parentId: info.node.value },
      });
    }
    routerData
      .refetch()
      .then((result) => listToTreeData(result.data.routerAll as any));
  };
  // #endregion

  // #region role

  const refProTable = useRef<ActionType>();

  const columnsDefault = generatorColumns<RoleFragment>(FindRoleListDocument, [
    {
      key: 'id',
      render: (
        text: ReactNode,
        record: RoleFragment,
        index: number,
        action: UseFetchDataAction<RoleFragment>
      ) => <Link to={`/role/${text}`}>{text}</Link>,
    },
  ] as SetColumnType[]) as ProColumns<RoleFragment>[];

  /**
   * 定义列
   */
  const [columns] = useImmer(columnsDefault);

  const { refetch } = useFindRoleListQuery({ skip: true });

  /**
   * 根据router 加载role
   */
  const routerRoleData = useFindRouterRoleListQuery({ skip: true });
  /**
   * 路由对应角色
   */
  const [proTableSel, setProTableSel] = useImmer<
    Maybe<Array<Maybe<string> | undefined>>
  >([]);

  const [oldRouterRoleList, setOldRouterRoleList] = useImmer<
    Maybe<
      ReadonlyArray<
        Maybe<{ readonly __typename?: 'RouterRole' } & RouterRoleFragment>
      >
    >
  >([]);

  /**
   * 选择树节点
   */
  const handleTreeSelect = (selectedKeys: any[]) => {
    setRoutertem((draft) => {
      if (!selectedKeys || selectedKeys.length <= 0) {
        return nothing as any;
      }
      return selectedKeys[0];
    });
    if (!selectedKeys || selectedKeys.length <= 0) {
      setOldRouterRoleList((draft) => []);
      setProTableSel((draft) => []);
      return;
    }
    routerRoleData
      .refetch({
        param: {
          where: {
            routerId: selectedKeys[0],
          },
        },
      })
      .then((result) => {
        const routerRoleList = result.data.routerRoleAll
          ?.filter((p) => p?.roleId)
          .map((p) => p?.roleId);
        setOldRouterRoleList((draft) => result.data.routerRoleAll);
        setProTableSel((draft) => routerRoleList);
      });
  };

  // #endregion

  const [routerRoleBulk] = useRouterRoleBulkMutation();
  const [routerRoleDestroy] = useRouterRoleDestroyMutation();

  /**
   * 更新保存路由对应的角色
   */
  const handleConfirm = async () => {
    const delList = differenceBy(
      oldRouterRoleList,
      proTableSel?.map((p) => ({ roleId: p })) as any,
      'roleId'
    );
    const addList = differenceBy(
      proTableSel?.map((p) => ({ roleId: p })),
      oldRouterRoleList as any,
      'roleId'
    );
    const savePromise = {
      bulkSave:
        addList && addList.length > 0
          ? routerRoleBulk({
              variables: {
                param: addList.map((p) => {
                  return {
                    roleId: p.roleId,
                    routerId: routerItem,
                  };
                }),
              },
            })
          : undefined,
      destroy:
        delList && delList.length > 0
          ? routerRoleDestroy({
              variables: {
                where: {
                  id: delList.map((p) => p?.id) as Array<string>,
                },
              },
            })
          : undefined,
    };
    return Bluebird.props(savePromise)
      .then((result) => {
        message.success(`${t('finish')}${t('success')}`);
      })
      .catch(gqlErrorMessage);
  };

  if (routerData.loading) {
    return <Spin />;
  }

  return (
    <>
      <ProCard split="vertical">
        <ProCard colSpan="30%">
          <div>
            <Title level={5}>{t('appName')}</Title>
            <Select style={{ width: '100%' }} onSelect={handleSelectSelect}>
              {keys(APP_NAME).map((p) => {
                return (
                  <Option key={p} value={p}>
                    {get(APP_NAME, p)}
                  </Option>
                );
              })}
            </Select>
          </div>
          <div>
            <ProCard
              title={t('RouterList')}
              tooltip={t('canDrop')}
              size="small"
            >
              <Tree
                className="draggable-tree"
                key="value"
                draggable
                blockNode
                onDrop={handleDrop}
                treeData={treeData as any}
                onSelect={handleTreeSelect}
              />
            </ProCard>
          </div>
        </ProCard>
        <ProCard title={t('RoleMapper')} headerBordered>
          <div>
            <ProTable<RoleFragment>
              columns={columns}
              actionRef={refProTable}
              request={async (params, sorter, filter) => {
                return refetch({
                  param: {
                    limit: params.pageSize,
                    offset:
                      (toInteger(params.current) - 1) *
                      toInteger(params.pageSize),
                    order: proSortToOrder(sorter),
                    where: {
                      appName: appNameItem,
                      ...filtersToWhere([params, filter], whereModel),
                    },
                  },
                }).then((result) => {
                  return {
                    data: result.data.roleAll as Array<RoleFragment>,
                    total: toInteger(result.data?.roleCount),
                    success: true,
                  };
                });
              }}
              rowKey="id"
              pagination={{
                showQuickJumper: true,
                hideOnSinglePage: true,
              }}
              search={{
                layout: 'vertical',
              }}
              dateFormatter="string"
              toolBarRender={false}
              debounceTime={toInteger(process.env.REACT_APP_ANTD_DEBOUNCE)}
              options={{ density: true }}
              rowSelection={{
                selectedRowKeys: proTableSel as string[],
                onChange: (selectedRowKeys, selectedRows) => {
                  setProTableSel((draft) => selectedRows.map((p) => p.id));
                  // 设置role勾选
                },
                selections: [Table.SELECTION_ALL, Table.SELECTION_INVERT],
              }}
            />
          </div>
          <Popconfirm title={t('sureSave')} onConfirm={handleConfirm}>
            <Button disabled={!(appNameItem && routerItem)} type="primary">
              {t('save')}
            </Button>
          </Popconfirm>
        </ProCard>
      </ProCard>
    </>
  );
};

export default Router;
